.container {
  padding: 0.2em;
}

.container.grid {
  width: 50%;
}

.container.list {
  width: 100%;
}

.video {
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  box-shadow: 3px 3px 5px 0 rgba(191, 191, 191, 0.53);
  cursor: pointer;
  transition: transform 250ms ease-in;
}
.video:hover {
  transform: scale(1.02);
}
.thumnails {
  width: 40%;
  height: 100%;
}

.title {
  font-size: 0.8rem;
}
.channel {
  font-size: 0.6rem;
}
.metadata {
  margin-left: 0.2em;
}
